
import Vue from 'vue'
import App from './App'
// 引入Element组件库
import ElementUI from 'element-ui';
// 引入ElementUI的全部样式
import 'element-ui/lib/theme-chalk/index.css';

import VueRouter from 'vue-router'
import router from './router'

Vue.use(VueRouter)


Vue.config.productionTip = false

Vue.use(ElementUI)

new Vue({
    el: '#app',
    router: router,
    render: h => h(App),
})