<template>
    <div class="loginbody">
        <div class="logindata">
            <div class="logintext">
                <span>Welcome</span>
            </div>
            <div class="formdata">
                <el-form ref="form">
                    <el-form-item>
                        <el-input
                            v-model="account"
                            placeholder="请输入账号"
                        ></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input
                            v-model="password"
                            type="password"
                            placeholder="请输入密码"
                        ></el-input>
                    </el-form-item>
                </el-form>
        </div>
        <div class="tool">
            <div>
                <el-checkbox>记住密码</el-checkbox>
            </div>
            <div>
                <span class="shou">忘记密码？</span>
            </div>
        </div>
        <div class="butt">
            <el-button type="primary" @click="Login(account, password)">登录</el-button>
            <el-button class="shou" @click="Register">注册</el-button>
        </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: 'Login',
        data() {
            return {
                account: '',
                password: ''
            }
        },
        methods: {
            Login(account, password) {
                axios.post(`http://localhost:5000/login`, {account: account, password: password}).then(
                    response => {
                        console.log(response);
                        if(response.data.status === 0) {
                            this.$router.push({name: 'infomation'})
                        } else {
                            alert('密码错误，登录失败');
                        }
                        
                    },
                    error => {
                        console.log('请求失败！');
                        console.log(error);
                    }
                )
            },
            Register() {
                this.$router.push({name: 'register'})
            }
        }
    }
</script>

<style>
    .loginbody {
        width: 100%;
        height: 100%;
        min-width: 1000px;
        background-image: url("../assets/loginBackground.jpg");
        background-size: 100% 100%;
        background-position: center center;
        overflow: auto;
        background-repeat: no-repeat;
        position: fixed;
        line-height: 100%;
        padding-top: 150px;
    }

    .logintext {
        margin-bottom: 20px;
        line-height: 50px;
        text-align: center;
        font-size: 50px;
        font-weight: bolder;
        color: white;
        text-shadow: 2px 2px 4px #000000;
    }

    .logindata {
        width: 400px;
        height: 300px;
        margin-top: 10%;
        transform: translate(-50%);
        margin-left: 50%;
    }

    .tool {
        display: flex;
        justify-content: space-between;
        color: #606266;
    }

    .butt {
        margin-top: 10px;
        text-align: center;
    }

    .shou {
        cursor: pointer;
        color: #606266;
    }

    /*ui*/
        /* /deep/ .el-form-item__label {
        font-weight: bolder;
        font-size: 15px;
        text-align: left;
    }

    /deep/ .el-button {
        width: 100%;
        margin-bottom: 10px;
    } */
</style>