<template>
    <div class="registerbody">
        <div class="registerdata">
            <div class="registertext">
                <span>Welcome</span>
            </div>
            <div class="formdata">
                <el-form ref="form">
                    <el-form-item>
                        <el-input
                            v-model="account"
                            placeholder="请输入账号"
                        ></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input
                            v-model="password"
                            type="password"
                            placeholder="请输入密码"
                        ></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input
                            v-model="repassword"
                            type="password"
                            placeholder="请确认密码"
                        ></el-input>
                    </el-form-item>
                </el-form>
        </div>
        <div class="butt">
            <el-button type="primary" @click="Register(account, password, repassword)">提交</el-button>
            <el-button class="shou" @click="ReturnLogin">返回</el-button>
        </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: 'Register',
        data() {
            return {
                account: '',
                password: '',
                repassword: ''
            }
        },
        methods: {
            Register(account, password, repassword) {
                axios.post(`http://localhost:5000/register`, {account: account, password: password, repassword: repassword}).then(
                    response => {
                        console.log(response);
                        if(response.data.status === 0) {
                            alert('注册成功！');
                            this.$router.push({name: 'login'})
                        } else {
                            alert('注册失败！');
                        }
                        
                    },
                    error => {
                        console.log('请求失败！');
                        console.log(error);
                    }
                )
            },
            ReturnLogin() {
                this.$router.push({name: 'login'})
            }
        }
    }
</script>

<style>
    .registerbody {
        width: 100%;
        height: 100%;
        min-width: 1000px;
        background-image: url("../assets/loginBackground.jpg");
        background-size: 100% 100%;
        background-position: center center;
        overflow: auto;
        background-repeat: no-repeat;
        position: fixed;
        line-height: 100%;
        padding-top: 150px;
    }

    .registertext {
        margin-bottom: 20px;
        line-height: 50px;
        text-align: center;
        font-size: 50px;
        font-weight: bolder;
        color: white;
        text-shadow: 2px 2px 4px #000000;
    }

    .registerdata {
        width: 400px;
        height: 300px;
        margin-top: 10%;
        transform: translate(-50%);
        margin-left: 50%;
    }

    .tool {
        display: flex;
        justify-content: space-between;
        color: #606266;
    }

    .butt {
        margin-top: 10px;
        text-align: center;
    }

    .shou {
        cursor: pointer;
        color: #606266;
    }
</style>